import { Training_anggota } from 'src/app/sdk/hr/models/training-_anggota';
import { Training } from 'src/app/sdk/hr/models/training';
import { Component, OnInit } from '@angular/core';
import { DirektoriTrainingAnggotaService, DirektoriTrainingService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
  selector: 'app-training',
  templateUrl: './training.component.html',
  styleUrls: ['./training.component.scss']
})
export class TrainingComponent implements OnInit {

  constructor(
    private direktoriTrainingAnggotaService: DirektoriTrainingAnggotaService,
    private direktoriTrainingService: DirektoriTrainingService,
    private spinner: NgxSpinnerService,
    private router: Router,
    private pegawaiDetailService: PegawaiDetailService,
    private nzMessageService: NzMessageService
  ) { }

  id_pegawai: string = ''
  dataTraining: Training[] = []
  dataAnggota: Training_anggota = {}
  id_training: string = ''
  ngOnInit() {
    this.pegawaiDetailService.getIdPegawai().subscribe(data => {
      this.id_pegawai = data
      console.log('id pegawai paja tu ' + this.id_pegawai)
    })
    this.getTraining()
  }


  getTraining() {
    this.direktoriTrainingService.getDirektoriTrainings(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
      data => {
        this.dataTraining = data.data
      }
    )
  }

}
