import { Pegawai_sp } from 'src/app/sdk/hr/models/pegawai-_sp';
import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiSpService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
    selector: 'app-peringatan',
    templateUrl: './peringatan.component.html',
    styleUrls: ['./peringatan.component.scss']
})
export class PeringatanComponent implements OnInit {

    constructor(
        private direktoriPegawaiSpService: DirektoriPegawaiSpService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    id_pegawai: string = ''
    dataPegawaiSp: Pegawai_sp[] = []
    id_pegawai_sp: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getPegawaiSP()
    }


    getPegawaiSP() {
        this.direktoriPegawaiSpService.getDirektoriPegawaiSps(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataPegawaiSp = data.data
            }
        )
    }

}
