import { Projek } from 'src/app/sdk/hr/models/projek';
import { Component, OnInit } from '@angular/core';
import { DirektoriProjekService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
  selector: 'app-proyek',
  templateUrl: './proyek.component.html',
  styleUrls: ['./proyek.component.scss']
})
export class ProyekComponent implements OnInit {

  constructor(
    private direktoriProjekService: DirektoriProjekService,
    private spinner: NgxSpinnerService,
    private router: Router,
    private pegawaiDetailService: PegawaiDetailService,
    private nzMessageService: NzMessageService
  ) { }

  id_pegawai: string = ''
  dataProjek: Projek[] = []
  id_projek: string = ''
  ngOnInit() {
    this.pegawaiDetailService.getIdPegawai().subscribe(data => {
      this.id_pegawai = data
      console.log('id pegawai paja tu ' + this.id_pegawai)
    })
    this.getTraining()
  }


  getTraining() {
    this.direktoriProjekService.getDirektoriProjeks(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
      data => {
        this.dataProjek = data.data
      }
    )
  }

}
