import { TrainingComponent } from './lainnya/training/training.component';
import { NgModule } from '@angular/core'
import { Routes, RouterModule } from '@angular/router'
import { PegawiDetailComponent } from './pegawi-detail.component'
import { ProyekComponent } from './lainnya/proyek/proyek.component';
import { PeringatanComponent } from './lainnya/peringatan/peringatan.component';

const routes: Routes = [
    {
        path: '',
        component: PegawiDetailComponent,
        children: [
            {
                path: '',
                redirectTo: 'biodata/detail',
            },
            {
                path: 'biodata',
                loadChildren: () => import('./biodata/biodata.module').then(mo => mo.BiodataModule),
            },
            {
                path: 'hubungan-kerja',
                loadChildren: () => import('./hubungan-kerja/hubungan-kerja.module').then(mo => mo.HubunganKerjaModule),
            },
            {
                path: 'klaim',
                loadChildren: () => import('./klaim/klaim.module').then(mo => mo.KlaimModule),
            },
            {
                path: 'lainnya/training',
                component: TrainingComponent
            },
            {
                path: 'lainnya/lisensi',
                loadChildren: () => import('./lainnya/lisensi/lisensi.module').then(mo => mo.LisensiModule),
            },
            {
                path: 'lainnya/perjalanan-dinas',
                loadChildren: () => import('./lainnya/perjalanan-dinas/perjalanan-dinas.module').then(mo => mo.PerjalananDinasModule),
            },
            {
                path: 'lainnya/proyek',
                component: ProyekComponent
            },
            {
                path: 'lainnya/peringatan',
                component: PeringatanComponent
            },
            {
                path: 'cuti',
                loadChildren: () => import('./cuti/cuti.module').then(mo => mo.CutiModule),
            },
            {
                path: 'lembur',
                loadChildren: () => import('./lembur/lembur.module').then(mo => mo.LemburModule),
            },

            {
                path: 'pengaturan-cuti',
                loadChildren: () => import('./pengaturan-cuti/pengaturan-cuti.module').then(mo => mo.PengaturanCutiModule),
            },
            {
                path: 'absensi',
                loadChildren: () => import('./absensi/absensi.module').then(mo => mo.AbsensiModule),
            },
            {
                path: 'pengaturan-absensi',
                loadChildren: () => import('./pengaturan-absensi/pengaturan-absensi.module').then(mo => mo.PengaturanAbsensiModule),
            },
            {
                path: 'payroll',
                loadChildren: () => import('./payroll/payroll.module').then(mo => mo.PayrollModule)
            },
            {
                path: 'complaints',
                loadChildren: () => import('./request/complaints/complaints.module').then(mo => mo.ComplaintsModule)
            },
            {
                path: 'expenses',
                loadChildren: () => import('./request/expenses/expenses.module').then(mo => mo.ExpensesModule)
            },
            {
                path: 'hotel',
                loadChildren: () => import('./request/hotel/hotel.module').then(mo => mo.HotelModule)
            },
            {
                path: 'ticket',
                loadChildren: () => import('./request/ticket/ticket.module').then(mo => mo.TicketModule)
            },
            {
                path: 'transport',
                loadChildren: () => import('./request/transport/transport.module').then(mo => mo.TransportModule)
            },
            {
                path: 'uniform',
                loadChildren: () => import('./request/uniform/uniform.module').then(mo => mo.UniformModule)
            },
            {
                path: 'pengaturan/user',
                loadChildren: () => import('./pengaturan/user/user.module').then(mo => mo.UserModule)
            },
            {
                path: 'pengaturan/resign',
                loadChildren: () => import('./pengaturan/resign/resign.module').then(mo => mo.ResignModule)
            },
            {
                path: 'data-pegawai',
                loadChildren: () => import('./lainnya/data-pegawai/data-pegawai.module').then(mo => mo.DataPegawaiModule)
            }

        ]
    },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class PegawiDetailRoutingModule { }
